@import '../../style/app.scss';
.burger {
  position: relative;
  width: 30px;
  height: 20px;
  z-index: 3;
  cursor: pointer;
}
.burger.open {
  align-self: flex-start;
}
.burger-line {
  position: absolute;
  width: 30px;
  height: 2px;
  background: $white;
  @media (max-width: 590px) {
    stroke: $dark100;
    fill: $dark100;
    background: $dark100;
  }
  &:nth-child(1) {
    top: 0;
  }
  &:nth-child(4) {
    bottom: 0;
  }
  &:nth-child(2),
  &:nth-child(3) {
    top: 9px;
  }
  &:nth-child(1).open,
  &:nth-child(4).open {
    display: none;
  }
  &:nth-child(2).open {
    transform: rotate(45deg);
    transform-origin: center;
  }
  &:nth-child(3).open {
    transform: rotate(-45deg);
    transform-origin: center;
  }
}
